.nomar0 .grid_20 {
	width: 20%;
	float: left;
	margin: 0px !important;
}
.nomar0 .grid_20 .row_full,
.nomar0 .row_full {
	width: 100% !important;
	float: left;
}
.nomar0 .grid_20 select {
	padding: 5px !important;
	height: 30px;
}
.nomar0 .grid_20 span.row_full,
.nomar0 span.row_full {
	margin: 0px !important;
	padding-bottom: 5px !important;
}
.pad_b_5 {
	padding-bottom: 5px;
}
.pad_r_10 {
	padding-right: 10px;
}
.grid_49 {
	width: 49%;
	float: left;
}
.mar_b_15 {
	margin-bottom: 15px;
}
.pos_relative {
	position: relative;
}
.secElm:nth-child(2n) {
	float: right;
}
.rmvCls {
	position: absolute;
	top: 0px;
	right: 0px;
	z-index: 9999999;
	font-size: 12px;
	font-weight: 600;
	color: rgb(252, 0, 34);
}
.dropdown-content {
	z-index: 999999999;
}

.add-question .field-label {
	width: 25%;
	float: left;
	padding: 10px 0;
	color: #656565;
	font-size: 13px;
}
.view-question-modal {
	position: fixed;
	top: 0px;
	left: 0px;
	z-index: 99999;
	height: 100%;
	font-size: 1rem;
	background: rgba(0, 0, 0, 0.7);
	.disp_flex {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.closeRemove {
		position: absolute;
		top: 5px;
		right: 5px;
		cursor: pointer;
		color: #777;
	}
	.midelmContent {
		width: 100%;
		border-radius: 5px;
		background: #fff;
		padding: 15px;
		text-align: center;
	}
	> div,
	.disp_flex {
		height: 100%;
	}
	ul,
	ol {
		list-style: none;
		margin: 0px;
		padding: 0px;
		font-size: 1rem;
	}

	.questUlElm li {
		width: 50%;
		float: left;
		padding-bottom: 0.5rem;
	}
	.questUlElm li span {
		width: 50%;
		float: left;
		text-align: left;
	}
	.questUlElm li span:first-child {
		font-weight: bold;
	}
	.questUlElm li span img {
		width: 100%;
	}

	.question {
		ol li {
			font-weight: 600;
			margin-bottom: 0.25rem;
		}
	}
	.question-option {
		ol {
			display: flex;
			flex-direction: column;
		}
		.option {
			li {
				display: flex;
				justify-content: start;
			}
			margin-bottom: 1rem;
		}
		span {
			text-align: left;
		}
	}
}
